.formContainer{
    width: 400px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid #75afdf;
    border-radius: 4px;
    box-shadow: 0px 5px 10px 2px rgba(8, 233, 244, 0.2) inset;
    background-color: rgb(233, 246, 252);
    padding: 20px;
}

.input{
    margin-left: 10px;
    margin-bottom: 20px;
    background-color: rgb(224, 245, 255);
    border-color: aquamarine;
    border-radius: 2px;
}

.input:hover,
.input:focus{
    box-shadow:0px 5px 10px 2px rgba(8, 233, 244, 0.2) inset;
}

.button{
    width: 100px;
    height: 40px;
    background-color: #9099f1;
    border-radius: 4px;
    border-color: aquamarine;
}

.button:hover,
.button:focus{
    background-color: #7bd4f7;
}