.container{
    list-style: none;
    width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid #75afdf;
    border-radius: 4px;
    box-shadow: 0px 5px 10px 2px rgba(8, 233, 244, 0.2) inset;
    background-color: rgb(233, 246, 252);
    padding: 20px;
}

.item{
    display: flex;
}

.button{
    width: 70px;
    height: 40px;
    background-color: #9099f1;
    border-radius: 4px;
    border-color: aquamarine;
    margin-left: 15px;
}

.button:hover,
.button:focus{
    background-color: #7bd4f7;
}